import fetch from 'isomorphic-fetch';
import Config from '../config/app.example.jsx';
var Func = {
    getViewportSize:function () {
        return {
            width: window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth,
            height: window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight
        };
    },
    //get url 
    joinUrl:function (url,params) {
        let paramsArray = [];
        Object.keys(params).forEach(key => paramsArray.push(key + '=' + encodeURIComponent(params[key])));
        if (url.search(/\?/) === -1) {
            url += '?' + paramsArray.join('&');
        } else {
            url += '&' + paramsArray.join('&');
        }
        return url;
    },
    getFetch:function (url,callback,method,data) {
        //
        callback = arguments[1] ? arguments[1] : function () {};
        method = arguments[2] ? arguments[2] : 'GET';
        data = arguments[3] ? arguments[3] : {};

        method = method.toUpperCase();
        let body;
        switch (method)
        {
            case 'GET': //get  getbody  url 
                url = this.joinUrl(url,data);
                body = undefined;
                break;
            case 'POST':
                body = JSON.stringify(data);
                break;
            default:
                url = this.joinUrl(url,data);
                body = undefined;
        }
        let header = {
            'Accept':'application/json',
            'Content-Type':'application/json',
            'Access-Token':window.sessionStorage.getItem('access-token'), // access-token
            // 'Access-Token':'lipeng', // access-token
            'Secret-Key':window.sessionStorage.getItem('secret-key'),     // secret-key
            'Channel':Config.channel                                      // 
        };
        let params = {
            method:method,
            headers:header,
        };
        fetch(url,params,body)
            .then(function (response) {
                if(response.status >= 400) {
                    throw new Error("Bad response from server");
                }
                return response.json();
            })
            .then(function (res) {
                callback(res.datas);
            })
    }
};

module.exports=Func;


